import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useEarnChopRule from '../../../hooks/useEarnChopRule';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Edit as EditIcon, Trash } from 'react-feather';
import earnChopRuleService from '../../../services/earnChopRuleService';
import DeleteConfirmCheck from '../../../components/DeleteConfirmCheck';
import { useTranslation } from 'react-i18next';
import Header from './Header';
// import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function EarnChopRuleList() {
  const { t } = useTranslation();
  const { earnChopRules, deleteEarnChopRule } = useEarnChopRule();

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [needDeleteEarnChopRule, setDeleteEarnChopRule] = React.useState({});

  const handleClickOpen = (earnChopRule) => {
    setOpen(true);
    setDeleteEarnChopRule(earnChopRule);
  };

  const handleDeleteEarnChopRule = async () => {
    await deleteEarnChopRule(needDeleteEarnChopRule.id);
    setOpen(false);
    setDeleteEarnChopRule({});
  };

  const handleClose = () => {
    setOpen(false);
  };

  const columns = [
    {
      title: t('Name'),
      field: 'name'
    },
    {
      title: t('Payment Type'),
      field: 'payment_type',
      render: (rowData) => t(`PaymentType.${rowData.payment_type}`)
    },
    {
      title: t('Type'),
      field: 'type',
      render: (rowData) => t(`Type.${rowData.type}`)
    },
    {
      title: t('Actions'),
      field: 'actions',
      render: (rowData) => (
        <>
          <IconButton component={RouterLink} to={`/earnChopRules/${rowData.id}/edit`}>
            <SvgIcon fontSize="small">
              <EditIcon />
            </SvgIcon>
          </IconButton>
          <IconButton onClick={() => handleClickOpen(rowData)}>
            <SvgIcon fontSize="small">
              <Trash />
            </SvgIcon>
          </IconButton>
        </>
      )
    }
  ];

  if (!earnChopRules) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Earn Chop Rule List')}>
      <Container maxWidth={false}>
        <Header />
        {earnChopRules && (
          <Box mt={3}>
            <MaterialTable
              title={t('Earn Chop Rule List')}
              columns={columns}
              data={earnChopRules}
            />
          </Box>
        )}
      </Container>
      <DeleteConfirmCheck
        open={open}
        handleClose={handleClose}
        handleDelete={handleDeleteEarnChopRule}
        checkText={needDeleteEarnChopRule.name}
      />
    </Page>
  );
}
