import { useState, useEffect, useCallback } from 'react';
import useIsMountedRef from './useIsMountedRef';
import earnChopRuleService from '../services/earnChopRuleService';

export default function useEarnChopRule() {
  const [earnChopRules, setEarnChopRules] = useState([]);
  const isMountedRef = useIsMountedRef();

  const getEarnChopRules = useCallback(async () => {
    const data = await earnChopRuleService.list();
    setEarnChopRules(data.data.data);
  }, [isMountedRef]);

  const deleteEarnChopRule = useCallback(
    async (id) => {
      await earnChopRuleService.delete(id);
      getEarnChopRules();
    },
    [getEarnChopRules]
  );

  useEffect(() => {
    getEarnChopRules();
  }, [getEarnChopRules]);

  return { earnChopRules, deleteEarnChopRule };
}
